<template>
    <div class="myFind">
        <!-- 轮播图 -->
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="(image, index) in images" :key="index">
                <van-skeleton :round="true" avatar-shape="square"  avatar  avatar-size=356px  :row="3" :loading="loading">
                     <img v-lazy="image.pic" />
                </van-skeleton>
            </van-swipe-item>
        </van-swipe>
        
        <!-- 多功能区 -->
        <van-grid :column-num="3">
           <van-grid-item   v-for="(value,index) in iconArr" :key="index" icon="photo-o" @click="goPages(value.name)">
               <template>
                   <van-skeleton :round="true"  avatar avatar-size=45px  :loading="loading">
                           <span class="iconfont" :class="value.icon" ></span>
                   </van-skeleton>
                   <van-skeleton title row-width=100%  :loading="loading">
                           <span class="til">{{value.title}}</span>
                    </van-skeleton>
                   
               </template>
           </van-grid-item>
        </van-grid>
       
       <!-- 推荐歌单 -->
        <div class="songRecom">
            <div class="heras">
                <van-skeleton title row-width=100%  :row="2" :loading="loading">
                    <span>推荐歌单</span>
                </van-skeleton>
                <van-skeleton title row-width=100%  :row="2" :loading="loading">
                    <span @click="goPages('Menu')">查看更多</span>
                </van-skeleton>
                
            </div>
            <div class="bodys">
                <div class="wins">
                        <div class="songList" v-for="(item,index) in songRecom" :key="index" @click="goPaths('Playlist',item.id)">
                            <van-skeleton :round="true" avatar-shape="square" avatar avatar-size=100px :row="1" :loading="loading">
                                <img :src="item.picUrl"/>
                            </van-skeleton>
                            <van-skeleton title row-width=100%  :row="1" :loading="loading">
                               <p>{{item.name}}</p>
                            </van-skeleton>
                            <van-skeleton title row-width=100%  :row="1" :loading="loading">
                               <span class="hot iconfont icon-sanjiaoxing">{{item.playCount | ff_files}}</span>
                            </van-skeleton>
                            
                        </div>
                </div>
            </div>
        </div>


        <!-- 私人定制 -->
         <div class="songRecom siren">
            <div class="heras">
                <van-skeleton title row-width=100% :row="3" :loading="loading">
                        <span>
                            不可错过的新品流行
                            <van-skeleton title row-width=100%  :row="3" :loading="loading">
                            <span class="si">私人定制</span>
                            </van-skeleton>
                        </span>
                </van-skeleton>
                
                
            </div>
            <div class="bodys">
                <div class="wins">
                        <ul class="list" v-for="(bogItem,i) in SirenRecom" :key="i">
                                 <li class="listItem" v-for="(samllItem,j) in bogItem" :key="j">
                                            <van-skeleton title avatar :row="3" :loading="loading">
                                                <img  v-lazy="samllItem.picUrl"/>
                                            </van-skeleton>
                                            
                                            <div class="center">
                                                <van-skeleton title row-width=100% :row="3" :loading="loading">
                                                    <span>{{samllItem.name}}</span>
                                                </van-skeleton>
                                                <van-skeleton title row-width=100% :row="3" :loading="loading">
                                                    <span>-{{samllItem.song.artists[0].name}}</span>
                                                </van-skeleton>
                                                <van-skeleton title row-width=100% :row="3" :loading="loading">
                                                    <span class="iconfont " :class="samllItem.isItemPlay ? 'icon-ziliaozhongxin' : 'icon-zanting' " @click="ff_songs(samllItem.id,samllItem.name,samllItem.picUrl)" :index="samllItem.id"></span>
                                                </van-skeleton>
                                                
                                            </div>
                                   </li>              
                        </ul>
                </div>
            </div>
        </div>
       
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import '../assets/less/Find.less'
    export default {
        name : 'Find',
        data(){
            return{
                images : [],
                iconArr : [
                    {icon : 'icon-rili-copy'  , title : '每日推荐'  , name : 'Recom'},
                    // {icon : 'icon-FM1'        , title : '私人FM'    , name : 'Fm'},
                    {icon : 'icon-gedan2'     , title : '歌单广场'      , name : 'Menu'},
                    {icon : 'icon-paihangbang', title : '排行榜'    , name : 'Ranking'},
                ],
                songRecom  : [],
                sirenRecom : [],
                itemRecom  : [],
                loading    : true,
            }
        },
        methods:{
            goPages(name){
                this.$router.push({name})
            },
            goPaths(name,id){
               this.$router.push({name,query:{id}});
            },
            ff_Recom(){
                this.axios({
                    url:"/personalized",
                    method: 'GET',
                    params:{
                        limit:6
                    }
                })
                .then(res=>{
                    
                    if(res.data.code == 200){
                         this.songRecom = res.data.result;
                       
                    }
                })
                .catch(err=>{
                    console.log(err)
                })

            },
            // 海报资源请求
            ff_banner(){
                this.axios({
                    url    : '/banner',
                    method : 'GET',
                    params : {
                        type : 2
                    }
                })
                .then(res=>{
                 
                    if(res.data.code == 200){
                         this.images = res.data.banners.slice(0,6);
                         console.log(this.images)
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            // 推荐新音乐
            ff_songData(){
               this.axios({
                    url : '/personalized/newsong',
                    method : 'GET',
                    params : {
                        limit : 12
                    }

               })
               .then(res=>{
                   if(res.data.code == 200){
                         this.$toast.clear();
                         for(let i=1;i<=res.data.result.length;i++){
                             let a = res.data.result[i-1];
                             a.isItemPlay = true;

                             this.itemRecom.push(a);
                             if(i % 3 == 0){
                                 this.sirenRecom.push(this.itemRecom);
                                 this.itemRecom = [];
                             }
                         }
                         setTimeout(()=>{
                                this.loading = false
                         
                         },500)
                        
                        // 修改SirenRecom
                        this.$store.commit('gaiSirenRecom',this.sirenRecom)
                          
                         console.log(this.sirenRecom)
                   }
                   
               })
               .catch(err=>{
                   console.log(err)
               })
            },
            // this.$store.commit('gaiMusic',musicObj)
            // 获取歌曲url
            ff_songs(id,name,imgUrl){
                this.sirenRecom.forEach(val=>{
                    console.log(val)
                    val.forEach(item=>{
                        if(item.id == id){
                            item.isItemPlay = false;
                        }else{
                            item.isItemPlay = true;
                        }
                    })
                })

                if(this.songsTime){
                     clearTimeout(this.songsTime)
                }

                this.songsTime = setTimeout(() => {

                         
                        let  musicObj = {};
                        this.axios({
                            url : '/song/url',
                            method : 'GET',
                            params : {
                                id
                            }
                        })
                        .then(res=>{
                            console.log(res)
                            if(res.data.code == 200){
                                musicObj.name     = name;
                                musicObj.url      = res.data.data[0].url;
                                musicObj.imgUrl   = imgUrl;
                                musicObj.id       = id;
                                musicObj.isFlash  = true;
                                musicObj.isPlay   = true;
                                this.$store.commit('gaiMusic',musicObj)
                            }
                        })
                        .catch(err=>{
                            console.log(err)
                        })
                    
                }, 1000);
                 console.log(id,name,imgUrl)
                
            }
            
        },
        computed: {
                ...mapState({
                    music      : 'song',
                    SirenRecom : 'SirenRecom',
                })
            
        },
        created(){
            this.$toast.loading({
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
            });
            // 海报资源
            this.ff_banner();

            this.ff_Recom();

            this.ff_songData();

            
        },
        filters:{
              ff_files(number){
                    let numStr = '';
                    numStr = `${number.toString().slice(0,-4)}万` 
                    return numStr 
              }
        },
        watch:{
            music(news,olds){
                console.log('1212')
                 if(news.isPlay){
                  
                }
            }                        
        }
    }
</script>

<style lang="less" scoped>

</style>